}

.use-cases-benefits {

  &__list {
    display: flex;
    justify-content: center;

    @media all and (max-width: 770px) {
      flex-wrap: wrap;
      max-width: 450px;
      margin: 0 auto;
    }
  }

  &__item {
    padding: 0 16px;
    text-align: center;
    flex-basis: 200px;

    @media all and (max-width: 770px) {
      flex-basis: 215px;
      padding: 0 10px;
      margin-bottom: 45px;
    }

    @media all and (max-width: 500px) {
