  &__icon-wrap {
    margin-bottom: 32px;

    @media all and (max-width: 770px) {
      margin-bottom: 24px;
    }

    @media all and (max-width: 480px) {
      margin-bottom: 21px;
    }
  }

  &__item-title {
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    margin-bottom: 24px;

    @media all and (max-width: 770px) {
      font-size: 18px;
      line-height: 21px;
    }
  }

  &__item-description {
    font-weight: 300;
    font-size: 12px;
    line-height: 18px;

    @media all and (max-width: 770px) {
      font-size: 14px;
      line-height: 20px;
    }
  }
