      line-height: 34px;
    }
  }

  .wpcf7-not-valid-tip {
    text-align: left;
  }
}

.flip-card {
  perspective: 1000px;
}

.flip-card-inner {
  position: relative;
  width: 100%;
  height: 248px;
  text-align: center;
  transition: transform 0.8s;
  transform-style: preserve-3d;

  @media all and (max-width:768px) {
    height: 244px;
  }

  @media all and (max-width: 620px) {
    height: 260px;
  }
}

.flip-card.card-action .flip-card-inner {
  transform: rotateY(-180deg);
}

.flip-card-front, .flip-card-back {
  position: absolute;
  width: 100%;
  max-width: 100%;
  height: 100%;
  backface-visibility: hidden;
  background: #ffffff;
  top: 0;
  left: 0;
}

.flip-card-front {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.flip-card-back {
  transform: rotateY(-180deg);
}

